	onShow() {
	
		let callflowid=this.callflowid
		let isApplet='1'
		let access_token=getToken()
		let data={isApplet:"1"}
		data=JSON.stringify(data)
		// this.gettemInfo();
		this.$store.dispatch('recording/getMainFlow',{callflowid,isApplet,access_token})
